@Component({
    components: { Table, Card, Forms, LogsDownloadExcel },
})
export default class VuePageClass extends Vue {
    //#region Variables
    private tableItem: IComponent.ITableItem = {
        path: ServerApiPath.Event_R,
        showPaging: true,
        showCheckbox: false,
        checkboxDisabled: true,
        datas: [],
        columns: [],
        paging: {
            pageSize: 10,
            page: 1,
            totalPages: 0,
            total: 0,
        },
        actions: {
            disableEdit: true,
            disablePerview: true,
            disableDelete: true,
        },

        // Option: only default sorting
        sorting: {
            field: 'when',
            orderEnum: ETableSorting.desc,
            order: 1,
        },
    };

    private tableParam: IComponent.ITableParam = {
        page: 1,
        pageSize: 10,
        sortingField: undefined,
        sortingOrderEnum: ETableSorting.none,
        sortingOrder: undefined,
    };

    private tableApiParam: Model.ITableApiParam = {
        paging: {
            page: 1,
            pageSize: 10,
        },
        sorting: {},
        keyword: undefined,
        what: undefined,
        startDate: undefined,
        endDate: undefined,
    };

    // card
    private cardItem: IComponent.ICardItem = {
        showHeader: true,
        showCollapse: true,
        visiable: true,
    };

    // select option
    private selectOptionItem: Model.ISelectOptionItem = {
        what: [],
    };

    // filter
    private filterDataOriginal: Model.IFilterData = {
        what: null,
        who: '',
        startDate: undefined,
        endDate: undefined,
    };
    private filterData: Model.IFilterData = { ...this.filterDataOriginal };

    private filterConfig: Model.IFilterConfigs = {
        what: JSON.parse(JSON.stringify(ServiceFormConfig.getFormConfig())),
        who: JSON.parse(JSON.stringify(ServiceFormConfig.getFormConfig())),
        startDate: JSON.parse(JSON.stringify(ServiceFormConfig.getFormConfig())),
        endDate: JSON.parse(JSON.stringify(ServiceFormConfig.getFormConfig())),
    };

    private filterConfigs: IComponent.IFormConfig[] = [];

    private formItem: Model.IFormItem = {
        isClearState: false,
        isSearch: false,
    };

    private eFormMode = EFormMode;

    private downloadItem: IViews.IDownloadItem = {
        isExcel: true,
        isCSV: false,
    };

    private loading = new Loading();
    //#endregion

    //#region Vue Life
    private async beforeMount(): Promise<void> {
        this.initData();
        await this.initTable();
        await this.initSelectOption();
    }
    //#endregion

    //#region Function

    //#region Init Table
    private initTableColumns(): void {
        this.tableItem.columns = [
            { title: this.$i18n.Common_NO, type: ETableRowType.autoIndex },
            { title: this.$i18n.Logs_EventType, key: 'what', sort: true },
            { title: this.$i18n.Logs_Username, key: 'who', sort: true },
            { title: this.$i18n.Logs_Message, key: 'message' },
            { title: this.$i18n.Logs_EventTime, key: 'when', sort: true },
        ];
    }

    private initTableParam(): void {
        const { paging, sorting }: IComponent.ITableItem = this.tableItem;

        this.tableParam = {
            page: paging?.page ?? 1,
            pageSize: paging?.pageSize ?? 10,
            sortingField: sorting?.field ?? undefined,
            sortingOrderEnum: sorting?.orderEnum ?? ETableSorting.none,
            sortingOrder: sorting?.order ?? undefined,
        };
    }

    private async initTable(): Promise<void> {
        this.initTableColumns();
        this.initTableParam();
        await this.tableReload(this.tableParam);
    }
    //#endregion

    //#region Init Data
    private initData(): void {
        this.initFilterConfig();
    }

    private initFilterConfig(): void {
        this.filterConfigs = [];

        // who
        this.filterConfig.who.id = 'who';
        this.filterConfig.who.label = this.$i18n.Logs_Username;
        this.filterConfig.who.placeholder = this.$i18n.Logs_Username;
        this.filterConfig.who.cols = 6;

        // what
        this.filterConfig.what.id = 'what';
        this.filterConfig.what.label = this.$i18n.Logs_EventType;
        this.filterConfig.what.placeholder = this.$i18n.Logs_EventType;
        this.filterConfig.what.formType = EFormType.single;
        this.filterConfig.what.cols = 6;

        // startDate
        this.filterConfig.startDate.id = 'startDate';
        this.filterConfig.startDate.label = this.$i18n.Logs_EventStartDate;
        this.filterConfig.startDate.placeholder = this.$i18n.Logs_EventStartDate;
        this.filterConfig.startDate.formType = EFormType.date;
        this.filterConfig.startDate.cols = 6;
        this.filterConfig.startDate.dateFormat = EDatetimeFormat.date_slash_YYYYMMDD;

        // endDate
        this.filterConfig.endDate.id = 'endDate';
        this.filterConfig.endDate.label = this.$i18n.Logs_EventEndDate;
        this.filterConfig.endDate.placeholder = this.$i18n.Logs_EventEndDate;
        this.filterConfig.endDate.formType = EFormType.date;
        this.filterConfig.endDate.cols = 6;
        this.filterConfig.endDate.dateFormat = EDatetimeFormat.date_slash_YYYYMMDD;

        this.filterConfigs = [this.filterConfig.who, this.filterConfig.what, this.filterConfig.startDate, this.filterConfig.endDate];
    }
    //#endregion

    //#region Init Select Option
    private initSelectOption(): void {
        this.initSelectOptionType();
    }

    private initSelectOptionType(): void {
        this.selectOptionItem.what = [
            // 1
            { objectId: Model.EEventType.serverStart, name: this.$i18n.Log_Enum_EEventType_serverStart },

            // 101
            { objectId: Model.EEventType.userLogin, name: this.$i18n.Log_Enum_EEventType_userLogin },
            { objectId: Model.EEventType.userLogout, name: this.$i18n.Log_Enum_EEventType_userLogout },
            { objectId: Model.EEventType.userLoginFail, name: this.$i18n.Log_Enum_EEventType_userLoginFail },
            { objectId: Model.EEventType.userMaintainSessionId, name: this.$i18n.Log_Enum_EEventType_userMaintainSessionId },

            // 111
            { objectId: Model.EEventType.userCreate, name: this.$i18n.Log_Enum_EEventType_userCreate },
            { objectId: Model.EEventType.userUpdate, name: this.$i18n.Log_Enum_EEventType_userUpdate },
            { objectId: Model.EEventType.userDelete, name: this.$i18n.Log_Enum_EEventType_userDelete },
            { objectId: Model.EEventType.userChangePassword, name: this.$i18n.Log_Enum_EEventType_userChangePassword },
            { objectId: Model.EEventType.userForget, name: this.$i18n.Log_Enum_EEventType_userForget },
        ];
    }

    //#endregion

    //#region Table
    private async tableGetApiData(tableParam: IComponent.ITableParam): Promise<void> {
        const { page, pageSize, sortingField, sortingOrder }: IComponent.ITableParam = tableParam;

        this.tableItem.paging.page = page ?? this.tableItem.paging.page;
        this.tableItem.paging.pageSize = pageSize ?? this.tableItem.paging.pageSize;

        const { paging }: IComponent.ITableItem = this.tableItem;

        this.tableApiParam.paging.page = paging.page ?? 1;
        this.tableApiParam.paging.pageSize = paging.pageSize ?? 10;

        this.tableApiParam.sorting = {};
        if (sortingField !== undefined && sortingOrder !== undefined) {
            this.tableApiParam.sorting.field = sortingField;
            this.tableApiParam.sorting.order = sortingOrder;
        } else {
            delete this.tableApiParam.sorting;
        }

        const { who, what, startDate, endDate }: Model.IFilterData = this.filterData;

        if (who) {
            this.tableApiParam.keyword = who;
        } else {
            delete this.tableApiParam.keyword;
        }

        if (what?.objectId) {
            this.tableApiParam.what = what.objectId;
        } else {
            delete this.tableApiParam.what;
        }

        if (startDate) {
            this.tableApiParam.startDate = ServiceDatetime.dateStart(startDate).toISOString();
        } else {
            delete this.tableApiParam.startDate;
        }

        if (endDate) {
            this.tableApiParam.endDate = ServiceDatetime.dateEnd(endDate).toISOString();
        } else {
            delete this.tableApiParam.endDate;
        }

        this.loading.show();

        let param = JSON.parse(JSON.stringify(this.tableApiParam));

        let url: string = '';

        if (this.formItem.isSearch) {
            param.paging.page = 1;
            url = ServiceServerConfig.paramUrl(this.tableItem.path, param);
        } else {
            url = ServiceServerConfig.paramUrl(this.tableItem.path, param);
        }

        try {
            // const { data, status }: AxiosResponse = await this.axios.get(url, param);
            const { data, status } = this.getFakeData(tableParam);
            if (data) this.tableSetData(data);

            if (status === 200) {
                this.tableItem.paging.pageSize = data?.paging?.pageSize ? data?.paging?.pageSize : this.tableItem.paging.pageSize;
                this.tableItem.paging.page = data?.paging?.page ? data?.paging?.page : this.tableItem.paging.page;
                this.tableItem.paging.total = data?.paging?.total ? data?.paging?.total : this.tableItem.paging.total;
                this.tableItem.paging.totalPages = data?.paging?.totalPages ? data?.paging?.totalPages : this.tableItem.paging.totalPages;

                if (data?.results?.length === 0 && data?.paging?.page === 1) {
                    this.tableItem.paging.page = 1;
                    this.tableItem.paging.pageSize = 10;
                    this.tableItem.paging.total = 0;
                    this.tableItem.paging.totalPages = 1;
                }

                if (data?.results?.length === 0 && data?.paging?.page !== 1) {
                    this.loading.hide();
                    let reloadParam = JSON.parse(JSON.stringify(this.tableParam));
                    reloadParam.page = data?.paging?.page - 1;
                    reloadParam.pageSize = data?.paging?.pageSize;
                    this.tableReload(reloadParam);
                }

                if (data && data.results) this.tableSetData(data.results);

                this.formItem.isClearState = true;
            }
        } catch (e) {
            ServiceResponseFilter.catchError(this, e);
        } finally {
            this.loading.hide();
        }
    }

    private getFakeData(tableParam: IComponent.ITableParam): any {
        let data: any[] = JSON.parse(JSON.stringify(LOGS));

        // keyword
        if (!!this.tableApiParam?.keyword) {
            const keywordFields: string[] = ['who'];
            let keyword: string = this.tableApiParam.keyword.toLowerCase();
            data = data.filter((item) => {
                let isMatch: boolean = false;

                for (let field of keywordFields) {
                    let target: string = item[field];
                    if (!!target && target.toLowerCase().includes(keyword)) {
                        isMatch = true;
                        break;
                    }
                }

                return isMatch;
            });
        }

        // what
        if (!!this.tableApiParam?.what) {
            data = data.filter((item) => {
                return item.what === this.tableApiParam?.what;
            });
        }

        // startDate
        if (this.tableApiParam.startDate !== null && this.tableApiParam.startDate !== undefined) {
            data = data.filter((item) => {
                let itemStartDate = ServiceDatetime.dateStart(new Date(item.when));
                return itemStartDate >= new Date(this.tableApiParam.startDate);
            });
        }

        // endDate
        if (this.tableApiParam.endDate !== null && this.tableApiParam.endDate !== undefined) {
            data = data.filter((item) => {
                let itemEndDate = ServiceDatetime.dateEnd(new Date(item.when));
                return itemEndDate <= new Date(this.tableApiParam.endDate);
            });
        }

        // sorting
        if (!!tableParam && !!tableParam.sortingField && tableParam.sortingOrderEnum !== ETableSorting.none) {
            let sortingField: string = tableParam.sortingField;
            data = data.sort((a, b) => {
                if (tableParam.sortingOrder === 0) {
                    if (typeof a[sortingField] === 'number') {
                        return a[sortingField] - b[sortingField];
                    }

                    a[sortingField] = a[sortingField] || '';
                    b[sortingField] = b[sortingField] || '';
                    if (a[sortingField] > b[sortingField]) return 1;
                    if (a[sortingField] < b[sortingField]) return -1;
                } else {
                    if (typeof a[sortingField] === 'number') {
                        return b[sortingField] - a[sortingField];
                    }

                    a[sortingField] = a[sortingField] || '';
                    b[sortingField] = b[sortingField] || '';
                    if (a[sortingField] > b[sortingField]) return -1;
                    if (a[sortingField] < b[sortingField]) return 1;
                }

                return 0;
            });
        }

        // pagination
        let pageSize: number = tableParam?.pageSize || this.tableApiParam.paging.pageSize;
        let total: number = data.length;
        let totalPages: number = Math.ceil(total / pageSize);
        let page: number = tableParam?.page || this.tableApiParam.paging.page;
        if (page > totalPages) {
            page = totalPages || 1;
        }

        this.tableItem.paging.pageSize = pageSize;
        this.tableItem.paging.page = page;
        this.tableItem.paging.totalPages = totalPages;
        this.tableItem.paging.total = total;

        data = data.slice((page - 1) * pageSize, page * pageSize);

        return { data, status: 200 };
    }

    private async tableReload(tableParam: IComponent.ITableParam): Promise<void> {
        const { page, pageSize, sortingField, sortingOrder }: IComponent.ITableParam = tableParam;

        this.tableParam.page = page ?? this.tableItem.paging.page;
        this.tableParam.pageSize = pageSize ?? this.tableItem.paging.pageSize;
        this.tableParam.sortingField = sortingField ?? undefined;
        this.tableParam.sortingOrder = sortingOrder ?? undefined;

        await this.tableGetApiData(this.tableParam);
    }

    private tableSetData(result: Model.IServerResponseData[]): void {
        let tableData: Model.ITableData[] = [];

        (result || []).forEach((element) => {
            tableData.push({
                what: element.what ? this.$i18n[`Log_Enum_EEventType_${element.what}`] : '',
                who: element.who ?? '',
                message: this.resolveEventMessage(element, this),
                when: ServiceResolveText.resolveDate(element.when, EDatetimeFormat.datetime_slash_YYYYMMDDHHmmss),
            });
        });

        this.tableItem.datas = tableData;
    }
    //#endregion

    //#region Search
    private async searchData(tableParam: IComponent.ITableParam): Promise<void> {
        this.formItem.isSearch = true;
        await this.tableGetApiData(tableParam);
        this.formItem.isSearch = false;
    }
    //#endregion

    //#region Filter
    private fitlerConfigValues(value: any, disabledButton: boolean, id: string) {
        if (this.filterData.hasOwnProperty(id)) this.filterData[id] = value;
    }
    //#endregion

    //#region Resolve text
    private resolveEventMessage(data: any, vue: any = this) {
        return resolveEventMessage(data, vue);
    }
    //#region Filter

    //#endregion
}

