//#region Logs

export interface IServerResponseData {
    objectId: string;
    who: string;
    when: Date;
    what: EEventType;
    detail: any;
}

//#region Table
export interface ITableData {
    what: string;
    who: string;
    message: string;
    when: string;
}

export interface ITableApiParam {
    paging: {
        page: number;
        pageSize: number;
    };
    sorting?: {
        field?: string;
        order?: number;
    };
    keyword?: string;
    what?: string;
    startDate?: string;
    endDate?: string;
}
