//#region Select Option Item

export interface IWhat {
    objectId: EEventType;
    name: string;
}

export interface ISelectOptionItem {
    what: IWhat[];
}
//#endregion

//#region Filter
export interface IFilterData {
    what: IComponent.ISelectionBase;
    who: string;
    startDate: Date;
    endDate: Date;
}

export interface IFilterConfigs {
    what: IComponent.IFormConfig;
    who: IComponent.IFormConfig;
    startDate: IComponent.IFormConfig;
    endDate: IComponent.IFormConfig;
}
//#endregion

//#region FormData
export interface IFormItem {
    isClearState: boolean;
    isSearch: boolean;
}

