import { IServerResponseData, EEventType } from './models';
import Logs from './logs.vue';
export default Logs;

/**
 *
 * @param data
 */
export function resolveEventMessage(data: IServerResponseData, vue: Vue) {
    let result: string = '';
    const { who, detail }: IServerResponseData = data;

    const value = {
        ...detail,
        who,
    };

    switch (data.what) {
        // 1
        case EEventType.serverStart:
            result = replaceVariables(vue.$i18n.Log_FRSServerStart, value);
            break;

        // 101
        case EEventType.userLogin:
            result = replaceVariables(vue.$i18n.Log_Login, value);
            break;
        case EEventType.userLogout:
            result = replaceVariables(vue.$i18n.Log_Logout, value);
            break;
        case EEventType.userLoginFail:
            result = replaceVariables(vue.$i18n.Log_LoginFail, value);
            break;
        case EEventType.userMaintainSessionId:
            result = replaceVariables(vue.$i18n.Log_MaintainSessionId, value);
            break;

        // 111
        case EEventType.userCreate:
            result = replaceVariables(vue.$i18n.Log_CreateUser, value);
            break;
        case EEventType.userUpdate:
            result = replaceVariables(vue.$i18n.Log_UpdateUser, value);
            break;
        case EEventType.userDelete:
            result = replaceVariables(vue.$i18n.Log_DeleteUser, value);
            break;
        case EEventType.userChangePassword:
            result = replaceVariables(vue.$i18n.Log_ChangePassword, value);
            break;
        case EEventType.userForget:
            result = replaceVariables(vue.$i18n.Log_ForgotPassword, value);
            break;

        default:
            break;
    }

    if (!result) {
        result = JSON.stringify(data);
    }
    return result;
}

/**
 *
 * @param target
 * @param value
 * @returns
 */
function replaceVariables(target: string, value: any): string {
    const variables: string[] = ['who', 'account', 'username', 'name', 'personName', 'count', 'raw', 'first', 'address'];

    let result: string = target || '';
    value = value || {};

    variables.forEach((variable) => {
        let replaceValue = value[variable] || '';
        result = result.replace(new RegExp(`{{${variable}}}`, 'ig'), replaceValue);
    });

    return result;
}
