
































import { Vue, Component, Prop } from 'vue-property-decorator';
import { AxiosResponse } from 'node_modules/axios';

import WebXProgress, { EProgressMode } from '@advantech/web-x-component-progress';
import { EDatetimeFormat } from '@/../enums';
import { ServiceResponseFilter, ServiceServerConfig, ServiceDialog, ServiceDatetime, ServiceResolveText } from '@/../helpers';

import { ServerApiPath } from '@/config';
import { resolveEventMessage } from '.';
import { IViews } from '@/models';

import * as Model from './models';

import Loading from '@/../components/loading/loading.vue';

// Excel
import * as Excel from 'exceljs/dist/exceljs.min.js';
import { saveAs } from 'file-saver';

@Component({
    components: { WebXProgress },
})
export default class LogsDownloadExcel extends Vue {
    //#region Prop
    @Prop({
        type: Boolean, // Boolean, Number, String, Array, Object
        default: () => true,
    })
    private isExcel: boolean;

    @Prop({
        type: Boolean, // Boolean, Number, String, Array, Object
        default: () => true,
    })
    private isCSV: boolean;

    @Prop({
        type: Object, // Boolean, Number, String, Array, Object
        default: () => {},
    })
    private params: Model.ITableApiParam;
    //#endregion

    //#region Varables
    private pageSize: number = 100000;
    private parameters: Model.ITableApiParam = {
        paging: {
            pageSize: 10,
            page: 1,
        },
    };

    private excelItem: IViews.IExcelItem = {
        columnWidth: 20,
        rowHeight: 20,
        style: {
            font: {
                name: 'Microsoft JhengHei', // 字體
                color: { argb: '00000000' }, // 顏色
                size: 12,
                bold: false, // 粗體
            },
            alignment: { vertical: 'middle', horizontal: 'center', wrapText: true },
        },
        pagination: 100000,
        excelFileType: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
    };

    private progressItemOriginal: IViews.IProgressItem = {
        mode: EProgressMode.count,
        value: 0,
        max: 0,
        showProgress: false,
        howManyDataATime: 10,
        progressPercentage: '0 %',
    };
    private progressItem: IViews.IProgressItem = { ...this.progressItemOriginal };

    private loading = new Loading();
    //#endregion

    //#region Code excel download
    private async codingExcelGetData(page: number = 0): Promise<any> {
        if (this.params) {
            this.parameters = JSON.parse(JSON.stringify(this.params));
        }

        if (this.pageSize > 0 && page > 0) {
            this.parameters.paging = { pageSize: this.pageSize, page: page };
        }

        let url: string = '';

        if (this.parameters) {
            url = ServiceServerConfig.paramUrl(ServerApiPath.Event_R, this.parameters);
        } else {
            url = ServiceServerConfig.paramUrl(ServerApiPath.Event_R);
        }

        try {
            // const { status, data }: AxiosResponse = await this.axios.get(url);
            const { status, data }: any = this.getFakeData();
            if (status === 200) {
                if (data?.results && data?.results.length > 0) {
                    return data;
                } else if (data?.results && data?.results.length === 0) {
                    this.loading.hide();
                    const CONTENT_NODATA: string = this.$i18n.DownLoad_NoData;
                    const TITLE_ERROR: string = this.$i18n.Status_Error;
                    ServiceDialog.Error(this, CONTENT_NODATA, TITLE_ERROR);
                    this.showProgressItemClear();
                } else {
                    this.loading.hide();
                    return false;
                }
            }
        } catch (e) {
            ServiceResponseFilter.catchError(this, e);
            this.loading.hide();
        }
    }

    private getFakeData() {
        let res: any = JSON.parse(JSON.stringify(LOG_RESPONSE));
        let data: any[] = res.results;

        // startDate
        if (this.params.startDate !== null && this.params.startDate !== undefined) {
            data = data.filter((item) => {
                let itemStartDate = ServiceDatetime.dateStart(new Date(item.when));
                console.log(itemStartDate);

                return itemStartDate >= new Date(this.params.startDate);
            });
        }

        // endDate
        if (this.params.endDate !== null && this.params.endDate !== undefined) {
            data = data.filter((item) => {
                let itemEndDate = ServiceDatetime.dateEnd(new Date(item.when));
                return itemEndDate <= new Date(this.params.endDate);
            });
        }

        // pagination
        let pageSize: number = this.params.paging.pageSize;
        let total: number = data.length;
        let totalPages: number = Math.ceil(total / pageSize);
        let page: number = this.params.paging.page;
        if (page > totalPages) {
            page = totalPages || 1;
        }

        this.params.paging.pageSize = pageSize;
        this.params.paging.page = page;

        data = data.slice((page - 1) * pageSize, page * pageSize);

        res.results = data;

        return { data: res, status: 200 };
    }

    private async codingExcelExport(response, page, fileType: IViews.EFileType = IViews.EFileType.xlsx): Promise<void> {
        this.progressItem.showProgress = true;

        let excelData = response.results;
        let fileName: string = this.$i18n.Logs_Logs;
        let worksheetName: string = `page${page}`;
        let workbook: Excel.Workbook = new Excel.Workbook();
        let worksheet: Excel.worksheet = workbook.addWorksheet(worksheetName);
        let no: number = page * this.pageSize - this.pageSize + 1;

        const { columnWidth: width, style }: IViews.IExcelItem = this.excelItem;

        let columns = [
            { header: this.$i18n.Common_NO, key: 'no', width, style },
            { header: this.$i18n.Logs_EventType, key: 'what', width: width * 2.5, style },
            { header: this.$i18n.Logs_Username, key: 'who', width, style },
            { header: this.$i18n.Logs_Message, key: 'detail', width: width * 6, style },
            { header: this.$i18n.Logs_EventTime, key: 'when', width: width * 2, style },
        ];
        worksheet.columns = columns;

        for (let excelItem of excelData) {
            let what = excelItem.what ? this.$i18n[`Log_Enum_EEventType_${excelItem.what}`] : '';
            let who = excelItem.who ?? '';
            let detail = this.resolveEventMessage(excelItem);
            let when = ServiceResolveText.resolveDate(excelItem.when, EDatetimeFormat.datetime_dash_YYYYMMDDHHmmss);

            worksheet.addRow({ no, what, who, detail, when });

            this.progressItem.value += 1;
            no++;
        }

        if (workbook) {
            this.saveFile(workbook, `${fileName}_page${page}`, fileType);
        }
    }
    //#endregion

    //#region Shared function
    private resolveEventMessage(data: any, vue: any = this): any {
        return resolveEventMessage(data, vue);
    }

    private async batchDownloadExcel(): Promise<void> {
        this.loading.show();
        let tempData = await this.codingExcelGetData();
        if (tempData) {
            let pageData: any = tempData.paging;
            let total: number = pageData.total;
            let page: number = 1;
            let excelData: any = [];
            let loopLength = Math.ceil(total / this.pageSize);

            this.progressItem.max = total;

            for (let index = 0; index < loopLength; index++) {
                excelData = await this.codingExcelGetData(page);
                this.loading.hide();
                await this.codingExcelExport(excelData, page);
                page++;
                if (excelData.results.length < this.pageSize) {
                    this.showProgressItemClear();
                    break;
                }
            }
        }
    }

    private saveFile(workbook: any, fileName: string, fileType: IViews.EFileType = IViews.EFileType.xlsx): void {
        workbook[fileType].writeBuffer().then((data) => {
            let blob = new Blob([data], { type: this.excelItem.excelFileType });
            saveAs(blob, `${fileName}.${fileType}`);
        });
    }

    private showProgressItemClear(): void {
        this.progressItem = JSON.parse(JSON.stringify({ ...this.progressItemOriginal }));
    }

    //#endregion
}

