import { EProgressMode } from '@advantech/web-x-component-progress';
import * as Excel from 'exceljs';

//#region Excel
export interface IDownloadItem {
    isExcel: boolean;
    isCSV: boolean;
}
//#endregion

//#region Logs Download Excel
export interface IFont {
    name: string;
    color: object;
    size: number;
    bold: boolean;
}

export interface IExcelItemStyle {
    font: IFont;
    alignment: Partial<Excel.Alignment>;
}

export interface IExcelItem {
    columnWidth: number;
    rowHeight: number;
    style: IExcelItemStyle;
    pagination: number;
    excelFileType: string;
}

// template i18n
export interface ITemplateI18nItem {
    buttonExportCSV: string;
    buttonExportExcel: string;
}

// progress
export interface IProgressItem {
    mode: EProgressMode;
    value: number;
    max: number;
    showProgress: boolean;
    howManyDataATime: number;
    progressPercentage: string;
}

export enum EFileType {
    csv = 'csv',
    xlsx = 'xlsx',
}
//#endregion
